.FinishedQuiz {
  padding: 20px;
  color: #fff;
  border: 2px solid #fff;
  box-sizing: border-box;
}

.FinishedQuiz ul {
  list-style-type: 0;
  padding: 0;
  margin: 0;
}

.FinishedQuiz ul li {
  display: flex;
  align-items: center;
}

.FinishedQuiz ul li .icon-succsess {
  margin-left: 5px;
  color: green;
}

.FinishedQuiz ul li .icon-error {
  margin-left: 5px;
  color: red;
}